.users {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    max-width: 100%;
    width: 500px;
}

.userInfo {
    background-color: #a0c3ff;
    display: flex;
    margin: 10px;
    border-radius: 10px;
}

.userInfo > div {
    display: flex;
    margin: 7px;
}

.userInfo .avaAndButtonBlock {
    display: flex;
    flex-direction: column;
}

.userInfo .avaAndButtonBlock button {
    margin: 5px;
    border: 1px solid black;
    max-width: 100%;
    height: 25px;
    cursor: pointer;
    outline: none;
    border-radius: 5px;
    font-weight: bold;
}

.userInfo .avaAndButtonBlock button:hover {
    opacity: 0.7;
    cursor: pointer;
    transition: 0.4s;
}

.userAvatar {
    width: 100px;
    height: 100px;
    border-radius: 50%;
}

.userContent {
    display: flex;
    flex-direction: column;
    padding: 5px;
}

.userName {
    font-weight: bold;
    font-size: 20px;
}

.pageNavigation {
    margin: 10px;
}

.pageNumber {
    margin: 10px;
    padding: 3px;
    border-bottom: #6495ED 2px solid;
    cursor: pointer;
}

.selectedPage {
    margin: 10px;
    padding: 3px;
    border-bottom: black 2px solid;
    cursor: pointer;
    font-weight: bold;
}

.preloaderIMG{
    width: 100px;
    height:100px;
}